import React from 'react';
import PropTypes from 'prop-types';
import { Container } from './filter.css';

const Filter = ({ title, subtitle }) => (
  <Container>
    <p>{title}</p>
    <div>{subtitle}</div>
  </Container>
);

Filter.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string
};

Filter.defaultProps = {
  title: '',
  subtitle: ''
};

export default Filter;
