import React from 'react';
import { Container } from './priceRange.css';
import PriceRangeIcon from 'images/price_range.svg';
const PriceRange = () => (
  <Container>
    <p>Price Range</p>
    <div>Average trip price is $20</div>
    <img height="80" src={PriceRangeIcon} alt="price range" />
    <div>$10 - $1000+</div>
  </Container>
);

export default PriceRange;
