import React from 'react';
import PropTypes from 'prop-types';
import { SidebarSection as StyledSidebarSection } from './sidebarSection.css';

const SidebarSection = ({ children }) => <StyledSidebarSection>{children}</StyledSidebarSection>;

SidebarSection.propTypes = {
    children: PropTypes.node.isRequired,
};

export default SidebarSection;