import React, { Component } from 'react';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Layout from 'components/layout';
import Header from 'components/searchNav';
import HtmlFromMarkdown from 'components/htmlFromMarkdown';
import Wrapper from 'components/wrapper';
import BasicSection from 'components/basicSection';
import PriceRange from 'components/priceRange';
import DetailedFilter from 'components/detailedFilter';
import SidebarSection from 'components/SidebarSection';
import SearchViewSection from 'components/searchViewSection';
import SearchWrapper from 'components/searchWrapper';

export default class Search extends Component {
  render() {
    const { data } = this.props;
    const opts = data.searchJson;
    return (
      <Layout>
        <Header altImage={opts.headerImageAlt} />
        <SearchWrapper>
          <SidebarSection>
            <BasicSection />
            <PriceRange />
            <DetailedFilter />
          </SidebarSection>
          <SearchViewSection>
            search view section
          </SearchViewSection>
        </SearchWrapper>
      </Layout>
    );
  }
}
export const query = graphql`
  query SearchQuery {
    searchJson {
      title
      headerImageAlt
      content {
        childMarkdownRemark {
          html
        }
      }
    }
  }
`;

Search.propTypes = {
  data: PropTypes.shape({
    searchJson: PropTypes.object.isRequired,
  }),
};

Search.defaultProps = {
  data: undefined,
};
