import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

// eslint-disable-next-line import/prefer-default-export
export const Container = styled.div`
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
  p {
    margin: 4px 0;
    font-size: 17px;
    color: #555555;
    font-weight: bold;
  }
  div {
    font-size: 8px;
    color: #4E4E4E;
  }
  img {
    margin: 1rem 0;
  }
  ${MEDIA.DESKTOP`
    margin: 0;
  `};
`;
