import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import posed from 'react-pose';
import Nav from 'components/searchNav/nav';
import Icon from 'images/logo_black.svg';
import { Container, Input } from './searchNav.css';

// Example of a component-specific page transition
const AnimatedContainer = posed.div({
  enter: {
    y: 0,
    transition: {
      ease: 'easeInOut',
    },
  },
  exit: {
    y: '-100%',
    transition: {
      ease: 'easeInOut',
    },
  },
});

const SearchNav = ({ altImage }) => (
  <AnimatedContainer>
    <Container>
      <Link to="/">
        <img height="44" src={Icon} alt={altImage} />
      </Link>

      <Input placeholder="E.g. Nature, Mountain, Chile" />
      <Nav />
    </Container>
  </AnimatedContainer>
);

SearchNav.propTypes = {
  altImage: PropTypes.string.isRequired,
};

export default SearchNav;
