import React from 'react';
import Title from 'components/title';
import { Container, Detail, Element, Date } from './basicSection.css';

const BasicSection = () => (
  <Container>
    <Title as="h2">The basics</Title>
    <Detail>
      <Element>
        <h6>destinations</h6>
        <div>Asia, Africa</div>
      </Element>
      <Element>
        <h6>activities</h6>
        <div>Rafting, Camping, Fishing</div>
      </Element>
      <Date>
        departure date
      </Date>
      <Element>
        <h6>flexible departure</h6>
        <div>3 weeks</div>
      </Element>
    </Detail>
  </Container>
);

export default BasicSection;
